<template>
  <v-container>
    <h3>New Employee</h3>
    <v-form @submit.prevent="addEmployee" ref="addEmployeeForm">
      <v-text-field
        label="Employee ID"
        required
        v-model="employee_id"
        :rules="idRules"
      ></v-text-field>
      <v-text-field
        label="Name"
        required
        v-model="name"
        :rules="nameRules"
      ></v-text-field>
      <v-text-field
        label="Department"
        required
        v-model="dept"
        :rules="deptRules"
      ></v-text-field>
      <v-text-field
        label="Position"
        required
        v-model="position"
        :rules="posRules"
      ></v-text-field>
      <v-btn type="submit" color="blue" class="mx-4 mt-3">Add Employee</v-btn>
      <v-btn color="red" class="mt-3" to="/">Cancel</v-btn>
      <p style="color: red;">{{ errorMessage ? errorMessage : "" }}</p>
    </v-form>
  </v-container>
</template>

<script>
import db from "@/components/firebaseinit.js";
export default {
  name: "NewEmployee",
  data() {
    return {
      employee_id: "",
      name: "",
      dept: "",
      position: "",
      errorMessage: "",
      nameRules: [
        value => !!value || "You must provide an employee's name",
        value => {
          if (value.length >= 3) {
            return true;
          } else {
            return "Employee's name should contain at least 3 characters.";
          }
        }
      ],
      deptRules: [value => !!value || "Department is required"],
      posRules: [value => !!value || "Position is required"],
      idRules: [value => !!value || "Employee ID is required"]
    };
  },
  methods: {
    addEmployee() {
      if (this.$refs.addEmployeeForm.validate()) {
        db.collection("employees")
          .add({
            employee_id: this.employee_id,
            name: this.name,
            dept: this.dept,
            position: this.position
          })
          .then(docRef => {
            console.log("Document written with ID: " + docRef.id);
            this.$router.push("/");
          })
          .catch(error => console.log(error));
      } else {
        this.errorMessage =
          "You must fill all the fields before submitting the form";
      }
    }
  }
};
</script>

<style scoped></style>
